<script>
import UUID from 'uuidjs'
import forEach from 'lodash/forEach'
import { find, fetchSources, addMarketGold } from '@/api/market-center'

export default {
  created() {
    this.initSources()
    // id
    const id = this.$route.params.id
    let routeTitle = ''
    if (id) {
      routeTitle = ''
      this.loadMarketGold(id)
    }
    this.routes[2].title = routeTitle
  },
  data() {
    return {
      loading: false,
      routes: [
        { title: '', to: { name: 'Admin' } },
        { title: '', to: { name: 'MarketCenterDefault' } },
        { title: '', to: { name: 'MarketCenterEdit' } }
      ],
      // 
      form: {
        id: UUID.genV1().hexNoDelim, // id
        type: '', // 
        source_id: '', // idapiID
        source_name: '', // 
        buyCalc_type: 1, // 01
        buyCalc_value: 0, // 
        saleCalc_type: 1, // 01
        saleCalc_value: 0, // 
        order: '', // 
        visible: 1, // 01
        decimal: 2,  // 
        remark: '' // 
      },
      sources: [], // api
      buyCalc_types: [
        { text: '', value: 1 },
        { text: '', value: 0 }
      ],
      saleCalc_types: [
        { text: '', value: 1 },
        { text: '', value: 0 }
      ],
      visibles: [
        { text: '', value: 0 },
        { text: '', value: 1 }
      ],
      // 
      rules: {
        type: [],
        source_id: [],
        order: [],
        decimal: []
      },
      snackbar: false,
      snackbarText: ''
    }
  },
  methods: {
    sourceInputHandle(input) {
      const target = this.sources.find(function (v) {
        return '' + v.value === '' + input
      })
      this.form.source_name = target.text
    },
    async loadMarketGold(id) {
      const { data } = await find({ id })
      const model = data[0]
      delete model._id
      this.form = data[0]
    },
    async initSources() {
      const { data } = await fetchSources()
      const sources = []
      for (const goldGroup of data) {
        for (const gold of goldGroup) {
          sources.push({ text: gold['SNAME'], value: gold['ID'] })
        }
      }
      this.sources = sources
    },
    async saveEdit() {
      const valid = await this.$validator.validateAll()
      if (!valid) return
      this.loading = true
      const { data } = await addMarketGold(this.form)
      this.snackbar = true
      this.snackbarText = data.success ? '' : ''
      this.loading = false
    },
    goBack() {
      this.$router.back()
    }
  },
  watch: {
    vErrors: {
      handler: function (val, oldVal) {
        forEach(this.rules, (val, key) => {
          this.rules[key] = [() => (this.vErrors.has(key) ? this.vErrors.first(key) : true)]
        })
      },
      deep: true
    }
  }
}
</script>


<template>
  <v-layout row wrap>
    <v-flex xs12>
      <v-breadcrumbs divider="/">
        <v-breadcrumbs-item v-for="route in routes" :to="route.to" :key="route.title">
          {{ route.title }}
        </v-breadcrumbs-item>
      </v-breadcrumbs>
    </v-flex>
    <v-flex xs12 class="content">
      <v-card class="elevation-0">
        <v-card-title primary-title>
          <h3 class="headline">{{ form.type}}</h3>
        </v-card-title>
        <v-container fluid>
          <v-layout row wrap>
            <v-flex xs12 sm10 md8>
              <form @submit.prevent="saveEdit">
                <v-layout row wrap>
                  <v-flex xs12 sm6>
                    <v-text-field v-model="form.type" counter max="20" data-vv-name="type" label="" data-vv-as="" :rules="rules.type" v-validate="'required'" required></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6>
                    <v-select :items="sources" data-vv-name="source_id" data-vv-as="" :rules="rules.source_id" v-model="form.source_id" @input="sourceInputHandle" label="" item-text="text" item-value="value" v-validate="'required'" required></v-select>
                  </v-flex>
                </v-layout>
                <v-layout row wrap justify-start align-baseline>
                  <span></span>
                  <v-btn-toggle mandatory :items="buyCalc_types" v-model="form.buyCalc_type" class="pr-3"></v-btn-toggle>
                  <v-text-field type="number" v-model.number="form.buyCalc_value">
                  </v-text-field>
                </v-layout>
                <v-layout row wrap justify-start align-baseline>
                  <span></span>
                  <v-btn-toggle mandatory :items="saleCalc_types" v-model="form.saleCalc_type" class="pr-3"></v-btn-toggle>
                  <v-text-field type="number" v-model.number="form.saleCalc_value">
                  </v-text-field>
                </v-layout>
                <v-layout row wrap>
                  <v-flex xs12 sm6>
                    <v-text-field type="number" data-vv-name="order" data-vv-as="" :rules="rules.order" label="" v-model.number="form.order" v-validate="'required'" required>
                    </v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6>
                    <v-text-field type="number" data-vv-name="decimal" data-vv-as="" :rules="rules.decimal" label="" v-model.number="form.decimal" v-validate="'required'" required>
                    </v-text-field>
                  </v-flex>
                </v-layout>
                <v-layout row wrap justify-start align-baseline>
                  <span></span>
                  <v-btn-toggle mandatory :items="visibles" v-model="form.visible" class="pr-3"></v-btn-toggle>
                </v-layout>
                <v-layout row wrap>
                  <v-flex xs12>
                    <v-text-field v-model="form.remark" multi-line counter max="200" data-vv-name="remark" label=""></v-text-field>
                  </v-flex>
                </v-layout>
                <v-layout row wrap>
                  <v-flex xs6 sm3>
                    <v-btn primary :loading="loading" dark block type="submit">
                      <v-icon right dark>save</v-icon>
                    </v-btn>
                  </v-flex>
                  <v-flex xs6 sm3>
                    <v-btn block dark secondary @click.native="goBack">
                      <v-icon right dark>keyboard_return</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>
              </form>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
      <v-snackbar secondary right v-model="snackbar">
        {{ snackbarText }}
        <v-btn light flat @click.native="snackbar = false">
          <v-icon light>close</v-icon>
        </v-btn>
      </v-snackbar>
    </v-flex>
  </v-layout>
</template>

<style lang="scss" scoped>
.operate {
  cursor: pointer;
}

.appbar-title {
  padding-left: 1em;
}
</style>



